.App {
    --header_height: 60px;
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    position: relative;
}

.App::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #D9D9D9;
    top: 37px;
    left: 0;
    display: block;
}

.App::after{
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #D9D9D9;
    bottom: -37px;
    left: 0;
    display: block;
}

